import React, { Component } from 'react';
import { connect } from "react-redux";
import { changeTitle, viewSidemenu } from "../actions/common";

import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';


class FeedbackContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      feedback: undefined,
    };
    this.goBack = this.goBack.bind(this);
    this.clickOption = this.clickOption.bind(this);
  }

  async componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    changeHeader('   ');
    viewMenu(false);
    // try {
    //   const response = await api('get','/api/v1/community');
    //   this.setState({boards: response.data.data});
    // } catch(e) {
    //   alert('   \n   ')
    // }
  }

  componentWillUnmount() {
    const { viewMenu } = this.props;
    viewMenu(true);
  }

  clickOption() {
    const {feedback} = this.state;
    this.setState({feedback: !feedback })
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  render() {
    const {feedback} = this.state;
    return (
      <div className="report-container">
        <div className="back-btn" onClick={this.goBack}>
          <img src={backImg} />
        </div>
        <div className={`register-btn ${feedback !== undefined ? "on" : ""}`} onClick={this.postApi}></div>
        <div className="content">
          <div className="desc">
               .
            <br />
                
            <br />
               .
          </div>
          <div className={`review-option ${feedback === true ? "on" : ""}`} onClick={this.clickOption}>  </div>
          <div className={`review-option ${feedback === false ? "on" : ""}`} onClick={this.clickOption}> </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,

});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FeedbackContainer);
