import React, { Component } from 'react';
import { connect } from "react-redux";
import { changeTitle, viewSidemenu } from "../actions/common";

import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';


class ReviewContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      participation : false,
      livelihood: false,
      focus: false,
      etc: false,
      mentor_comment: "",
    };
    this.goBack = this.goBack.bind(this);
    this.clickOption = this.clickOption.bind(this);
  }

  async componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    changeHeader('   ');
    viewMenu(false);
    // try {
    //   const response = await api('get','/api/v1/community');
    //   this.setState({boards: response.data.data});
    // } catch(e) {
    //   alert('   \n   ')
    // }
  }

  componentWillUnmount() {
    const { viewMenu } = this.props;
    viewMenu(true);
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  clickOption(e) {
    console.log(e.target)
    const {[e.target.id] : value} = this.state;
    console.log(value);
    this.setState({[e.target.id]: !value })
  }

  render() {
    const {participation, livelihood, focus, etc} = this.state;
    console.log("ETC :  ", etc)
    return (
      <div className="report-container">
        <div className="back-btn" onClick={this.goBack}>
          <img src={backImg} />
        </div>
        <div className={`register-btn ${participation || livelihood || focus || etc ? "on" : ""}`} onClick={this.postApi}></div>
        <div className="content">
          <div className="desc">
            <span>   .</span>
            <p className="sub-desc">(  )</p>
          </div>
          <div className={`review-option ${participation ? "on" : ""}`} onClick={this.clickOption} id="participation">    .</div>
          <div className={`review-option ${livelihood ? "on" : ""}`} onClick={this.clickOption} id="livelihood">   .</div>
          <div className={`review-option ${focus ? "on" : ""}`} onClick={this.clickOption} id="focus">    .</div>
          <div className={`review-option ${etc ? "on" : ""}`} onClick={this.clickOption} id="etc"> </div>
          {
            etc ? <input className="etc-comment" type="text" /> : null
          }
        </div>
      </div>
    )
  }
}

// {
//    "participation" : "true", // "  ~"  ,   false
//    "livelihood": "true", //" ~"  ,   false
//    "focus": "true", //" ~"  ,   false
//    "mentor_comment": " " //
// }

const mapStateToProps = ({ common }) => ({
  title: common.title,

});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ReviewContainer);
