import React, { Component } from 'react';
import { connect } from "react-redux";
import { changeTitle, viewSidemenu } from "../actions/common";

import Header from "./HeaderContainer";
import { categoryOption } from "../utils/static";
import Card from "../components/notice/card";
import api from "../utils/api";
import HeaderComponent from "../components/myclass/headerComponent";
import '../styles/notice.scss';
import searchImg from "../images/global/btn-search@2x.png";

class NoticeContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      pinned: [],
      unpinned: [],
      showSearch: true,
      category: false,
      params: '',
      keyword: '',
      searchTitle: ''
    }
    this.toggle = this.toggle.bind(this);
    this.showSearch = this.showSearch.bind(this);
    this.filterData = this.filterData.bind(this);
    this.changeHandler = this.changeHandler.bind(this);
  }

  componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    changeHeader('');
    viewMenu(true);
    this.getNoticeList();
  }

  componentDidUpdate(prevProps, prevState) {
    const {keyword, searchTitle} = this.state;
    if(prevState.keyword !== keyword || prevState.searchTitle !== searchTitle) this.getNoticeList();
  }

  async getNoticeList() {
    const {showSearch, params, keyword} = this.state;
    try {
      let url = '/api/v1/notice';
      if(showSearch && (keyword || params)) url += `?${params || 'category=1'}&search=${keyword}`
      const response = await api('get', url);
      this.setState({
        pinned: response.data.pinned,
        unpinned: response.data.unpinned,
      });
    } catch(e) {
      alert(`[ : ${e.response.status}]   .`);
    }
  }

  showSearch() {
    const {showSearch} = this.state;
    this.setState({showSearch: !showSearch});
  }

  filterData(params, searchTitle) {
    this.setState({ params, searchTitle });
  }

  toggle(type) {
    this.setState(prevState => ({
      [type]: !prevState[type]
    }));
  }

  changeHandler(e) {
    this.setState({[e.target.name]: e.target.value});
  }

  render() {
    const {pinned, unpinned, showSearch, category, params, keyword, searchTitle} = this.state;
    console.log("keyword  : ", keyword)
    console.log("params  : ", params)
    console.log("searchTitle  : ", searchTitle)
    return (
      <div className="notice-container">
        {
          showSearch ? (
            <div className="search-wrapper">
              <HeaderComponent
                type="category"
                params={params}
                toggle={this.toggle}
                dropdownOpen={category}
                optionList={categoryOption}
                filterData={this.filterData}
                searchTitle={searchTitle}
              />
              <input
                className="search-input"
                type="text"
                placeholder=" "
                name="keyword"
                onChange={this.changeHandler}
                value={keyword}
              />
            </div>
          ) : null
        }
        <div className="register-btn on" onClick={this.showSearch}>
          {
            showSearch ? "" : 
            <img className="search-img" src={searchImg} />
          }
        </div>
        <div className="notice-list">
          <div className="fixed-container">
            {
              pinned.map((notice, index) =>
                <Card key={index} notice={notice} />
              )
            }
          </div>
          <div className="normal-container">
            {
              unpinned.map((notice, index) =>
                <Card key={index} notice={notice} />
              )
            }
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  title: common.title,
});
const mapDispatchToProps = dispatch => ({
  changeHeader: title => dispatch(changeTitle(title)),
  viewMenu: view => dispatch(viewSidemenu(view)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(NoticeContainer);

