import React, { Component } from 'react';
import { connect } from "react-redux";

import { changeTitle, viewSidemenu } from "../actions/common";
import Header from "./HeaderContainer";
import Card from "../components/notice/card";
import api from "../utils/api";
import {classTime} from "../utils/static";
import '../styles/payment.scss';

const monthList = () => {
  const todayYear = new Date().getFullYear();
  const todayMonth = new Date().getMonth() + 1;
  const startYear = '2012';
  const monthArr = [];
  for (let i=startYear; i <= todayYear; i++) {
    for (let j=1; j<=12;j++) {
      if(i !== todayYear || (i === todayYear && j <= todayMonth)) monthArr.push(`${i}-${(`0${j}`).slice(-2)}`);
    }
  }
  return monthArr;
}

const pickerLang = {
  months: ['1', '2', 'Mar', 'Spr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
  from: 'From', to: 'To'
}

const makeText = m => {
  if (m && m.year && m.month) {
    return `${pickerLang.months[m.month-1]}. ${m.year}`
  }
  return '?';
}

const MonthBox = ({value, click}) => (
  <div className="box" onClick={()=> click()}>
    <span>{value}</span>
  </div>
)

