  onChangeDate(e) {
    console.log(e.target.name, e.target.value);
    this.setState({[e.target.name]: e.target.value})
  }

  async getData() {
    const {month, toDate, fromDate} = this.state;
    try {
      let url = '/api/v1/mentor/my_classes/payments';
      if(month) {
        url += `?months=${month}`
      } else if(toDate || fromDate) {
        const nowDate = new Date().getDate();
        const nowHour = new Date().getHours();
        const nowMin = new Date().getMinutes();
        let fromValue = '';
        let toValue = '';
        if(fromDate) fromValue = new Date(`${fromDate}-${nowDate} ${nowHour}:${nowMin}`).getTime();
        if(toDate) toValue = new Date(`${toDate}-${nowDate} ${nowHour}:${nowMin}`).getTime();
        url += `?from=${fromValue}&?to=${toValue}`;
      }

      const response = await api('get', url);
      console.log("response : ", response);
      this.setState({content: response.data});
    } catch(e) {
      alert(`[ : ${e.response.status}]     .`);
    }
  }

